
import bem from '@/utilities/bem';
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'UIInput',
    props: {
        placeholder: String,
        type: {
            type: String,
            default: 'text',
            validator: function (value: string) {
                return ['text', 'password', 'date', 'number'].indexOf(value) !== -1
            }
        },
        size: {
            type: String,
            default: 'large',
            validator: function (value: string) {
                return ['large', 'medium', 'small'].indexOf(value) !== -1
            }
        },
    },
    data(){
        return {
            bem: () => {
                return bem('ui-input')
            },
            modifiers: () => {
                return [
                    this.type,
                    this.size,
                    ...(this.active ? ['active'] : []),
                    ...(this.curve ? ['curve'] : [])
                ];
            }
        }
    }
});
