<template>
        
        <ul class="ui-pagination">
            <li>                
                <button 
                    class="ui-pagination__item ui-pagination__item--prev"
                    v-bind:class="currentPage === 1 ? 'ui-pagination__item--disabled' : ''"
                    v-on:click="changePage('prev')"
                ></button>
            </li>

            <li v-for="(p, index) in pages"  :key="index">
                <button 
                    class="ui-pagination__item" 
                    v-bind:class="(currentPage - 1) === index ? 'ui-pagination__item--current' : ''"
                    v-on:click="changePage('page', index + 1)"    
                >
                    {{index + 1}}
                </button>
            </li>

            <li>
                <button 
                    class="ui-pagination__item ui-pagination__item--next"
                    v-bind:class="currentPage === pages.length ? 'ui-pagination__item--disabled' : ''"
                    v-on:click="changePage('next')"
                ></button>
            </li>
        </ul>
    
</template>


<script lang="ts">
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'UIPagination',
    props: {
        currentPage: {
            type: Number,
            default: 1
        },
        totalItems: Number,
        itemsPerPage: Number
    },
    methods: {
        changePage(type: 'next' | 'prev' | 'page', page?: number) {
            if(type === 'page' && !page)return;

            let newPage = page || 0;
            if(type !== 'page'){
                newPage = type === 'next' ? this.currentPage + 1 : this.currentPage - 1
            }
                            
            if(newPage === 0 || newPage > this.pages.length)return;
            this.$emit('pageChanged', newPage);
        },
    },
    data(){
        return {            
        }
    },
    computed: {
        pages(): number[] {
            const numberOfPage = Math.ceil(Number(this.totalItems) / Number(this.itemsPerPage));
            return new Array(numberOfPage).fill('');
        }
    }
});
</script>


<style lang="scss" scoped>
.ui-pagination {
    display: flex;
    max-width: fit-content;

    &__item {
        width: 2.4rem;
        height: 2.4rem;
        border-radius: 50%;
        border: none;
        box-shadow: 3px 2px 4px rgba(0,0,0 ,.1), -3px -2px 5px #fff;

        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 700;
        outline: none;
        cursor: pointer;
        line-height: 1.5;
        font-family: inherit;
        font-size: 1.1rem;
        margin: 0 .5rem;

        transition: all .25s ease;
    }


    &__item--current,
    &__item--prev,
    &__item--next {
        background-color: #0088ce;
        color: #fff;
        box-shadow: inset 5px 5px 10px rgba(0,0,0,0.1), inset 0px 0px 2px #fff;
    }

    &__item--disabled {
        opacity: 0.3;
    }

    &__item--prev,
    &__item--next {
        position: relative;

        &::before {
            content: '';
            width: 28%;
            height: 28%;
            position: absolute;
            top:50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(322deg);
            border: 2px solid #fff;
            border-bottom: none;
            border-right: none;
        }
    }

    &__item--next {
        &::before {
            transform: translate(-50%, -50%) rotate(495deg);
        }
    }
} 

</style>




