import { defineComponent } from 'vue';
import UIButton from '@/components/ui/UIButton.vue';
import UIInput from '@/components/ui/UIInput.vue';
import UILoading from '@/components/ui/UILoading.vue';
import UIInputSearch from '@/components/ui/UIInputSearch.vue';

import logger from '@/utilities/logger';

export default defineComponent({
	name: 'Tracer',
	components: {
		UIButton,
        UIInput,
		UIInputSearch,
		UILoading
	},
	data() {
		return {
			webRTCCapable: false,
			showLoading: false
		}
	},
	methods: {
		initialize(){
			this.webRTCCapable = this.checkIfBrowserIsWebRTCCapable();
			if(!this.webRTCCapable)logger.warning('Browser is not WebRTC capable.');

			this.showLoading = true;

			setTimeout(() => {
				this.showLoading = false;
			}, 5000)
		},
		checkIfBrowserIsWebRTCCapable() {
            try {
                const RTCPeerConnection = window.RTCPeerConnection;
                const pc = new RTCPeerConnection();
                if(!pc)return false;
                return true;
            } catch (ex) {
                return false;
            }
        }
	},
	mounted() {
		this.initialize();
	}
});
