import { defineComponent } from 'vue';
import UIInputSearch from '@/components/ui/UIInputSearch.vue';
import UITab from '@/components/ui/UITab.vue';
import iTabItem from '@/interface/tab-item.interface';
import UITableRow from '@/components/ui/table/UITableRow.vue';
import UITableColumn from '@/components/ui/table/UITableColumn.vue';
import UIDivider from '@/components/ui/UIDivider.vue';
import UIPagination from '@/components/ui/UIPagination.vue';

import asset from '@/utilities/asset';

export default defineComponent({
	name: 'Import',
	components: {
		UIInputSearch,
		UITab,
		UITableRow,
		UITableColumn,
		UIDivider,
		UIPagination
	},
	data() {
		const data: {
			activeTab: iTabItem | null,
			tabItems: iTabItem[],
			tableData: { 
				id: string,
				shape: string, 
				systemId: string, 
				reference: string, 
				name: string, 
				submittedDate: string 
			}[],
			paginationPage: number
		} = {
			activeTab: { id: 'all', label: 'All' },
			tabItems: [
				{ id: 'all', label: 'All' },
				{ id: 'ums', label: 'UMS' },
				{ id: 'trace', label: 'Trace' },
				{ id: 'pms', label: 'PMS' },
			],
			tableData: [
				{ shape: 'shape', systemId: 'PMS', id: '1', reference: 'ref', name: 'name', submittedDate: '29/04/2021 - 11:18:25' },
				{ shape: 'shape', systemId: 'PMS', id: '2', reference: 'ref', name: 'name', submittedDate: '29/04/2021 - 11:18:25' },
				{ shape: 'shape', systemId: 'PMS', id: '3', reference: 'ref', name: 'name', submittedDate: '29/04/2021 - 11:18:25' },
				{ shape: 'shape', systemId: 'PMS', id: '4', reference: 'ref', name: 'name', submittedDate: '29/04/2021 - 11:18:25' },
				{ shape: 'shape', systemId: 'PMS', id: '5', reference: 'ref', name: 'name', submittedDate: '29/04/2021 - 11:18:25' }
			],
			paginationPage: 1
		}
		
		return data;
	},
	methods: {
		tabOnSelect(item: iTabItem | null) {
			this.activeTab = item;
		},
		onPageChanged(page: number) {
			this.paginationPage = page;
		},
		getImage( name: string) {
			return asset.getImage({
				path: 'shape/',
				name,
				ext: 'jpg'
			})
		}
	},
	mounted() {
		 
	}
});
