
import bem from '@/utilities/bem';
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'UITableColumn',
    props: {
        align: {
            type: String,
            default: 'left',
            validator: function (value: string) {
                return ['left', 'right', 'center'].indexOf(value) !== -1
            }
        },
        last: Boolean
    },
    data(){
        return {
            bem: () => {
                return bem('ui-table-column')
            }
        }
    }
});
