<template>
    <ul :class="bem().block([type])">
        <li 
            class="ui-tab__item"
            v-for="item in items" 
            :key="item.id"
        >
            <UIButton 
                v-on:click="selectItem(item)"
                :text="item.label" 
                type="secondary" 
                :curve="true" 
                size="small"
                :active="activeItem && activeItem.id === item.id"
            />
        </li>
    </ul>
</template>


<script lang="ts">
import { defineComponent, PropType } from 'vue';
import UIButton from '@/components/ui/UIButton.vue';
import iTabItem from '@/interface/tab-item.interface';
import bem from '@/utilities/bem';

export default defineComponent({
    name: 'UITab',
    components: {
        UIButton
    },
    props: {
        type: {
            type: String,
            default: 'default',
            validator: function (value: string) {
                return ['default', 'toggle'].indexOf(value) !== -1
            }
        },
        activeItem: {
            type: Object as PropType<iTabItem>,
        },
        items: {
            type: Object as PropType<iTabItem[]>,
            required: true
        }
    },
    methods: {
        selectItem(item: iTabItem) {
            if(this.activeItem && this.activeItem.id === item.id){
                this.$emit('onSelect', null);
                return;
            }
            this.$emit('onSelect', item)
        },
        bem() {
            return bem('ui-tab');
        }
    }
});
</script>


<style lang="scss" scoped>
.ui-tab{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    
    &__item{
        margin: 0 .5rem;
    }

    &--toggle {
        // padding: .45rem .02rem;
        border-radius: 2rem;
        background-image: linear-gradient(142deg, rgba(215,213,213,0.2) 0%, rgba(255,255,255,0.1) 50%);
        box-shadow: 3px 2px 4px rgba(#000, .2), -2px -2px 10px #fff;
    }
}
</style>


