import iShape from '@/interface/shape.interface';
import { defineComponent } from 'vue';

export default defineComponent({
	name: 'Standard',
	components: {
	},
	data() {
		const data: {
			shapes: iShape[]
		} = {
			shapes: [
				{ id: '1', label: '1', image: 'stdShape1' },
				{ id: '2', label: '2', image: 'stdShape2' },
				{ id: '3', label: '3', image: 'stdShape3' },
				{ id: '4', label: '4', image: 'stdShape4' },
				{ id: '5', label: '5', image: 'stdShape5' },
				{ id: '6', label: '6', image: 'stdShape6' },
				{ id: '7', label: '7', image: 'stdShape7' },
				{ id: '8', label: '8', image: 'stdShape8' },
			]
		}
		
		return data;
	},
	methods: {
		getImgUrl(image: string) {
			return require(`@/assets/images/shape/${image}.png`)
		}
	},
	mounted() {
		 
	}
});
