import IUserStoreInterface from "./user.interface";
import { USER_SET_AUTHENTICATED, USER_SET_COUNTER } from "./user.mutation-types";

const UserMutations = {
    [USER_SET_COUNTER]: (state: IUserStoreInterface) => state.counter++,
    [USER_SET_AUTHENTICATED]: (state: IUserStoreInterface) => state.authenticated = !state.authenticated
}

export default UserMutations;