import IUserStoreInterface from "./user.interface";
import { USER_GET_AUTHENTICATED, USER_GET_COUNTER } from "./user.mutation-types";

const UserGetters = {
    [USER_GET_COUNTER]: (state: IUserStoreInterface) => state.counter,
    [USER_GET_AUTHENTICATED]: (state: IUserStoreInterface) => state.authenticated

}

export default UserGetters;