<template>
    <div :class="bem().block()">
        <UIInput class="ui-input-search__input" :placeholder="placeholder" :size="size"/>
        <UIButton class="ui-input-search__button" :text="text" :size="size" type="primary"/>
    </div>
</template>


<script lang="ts">
import { defineComponent } from 'vue';
import UIButton from '@/components/ui/UIButton.vue';
import UIInput from '@/components/ui/UIInput.vue';
import bem from '@/utilities/bem';

export default defineComponent({
    name: 'UIInputSearch',
    components: {
        UIInput,
        UIButton
    },
    props: {
        placeholder: String,
        text: {
            type: String,
            default: 'Search'
        },
        size: {
            type: String,
            default: 'large',
            validator: function (value: string) {
                return ['large', 'medium', 'small'].indexOf(value) !== -1
            }
        },
    },
    data(){
        return {
            bem: () => {
                return bem('ui-input-search')
            },
            // modifiers: () => {
            //     return [
            //         this.type,
            //         this.size,
            //         ...(this.active ? ['active'] : []),
            //         ...(this.curve ? ['curve'] : [])
            //     ];
            // }
        }
    }
});
</script>


<style lang="scss" scoped>
.ui-input-search {
    width: 100%;
    display: flex;
    
    &__input {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        flex: 1;
    }

    &__button {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        width: auto;
    }
    
}
</style>


