

const bem = (b: string) => {

    const generateModifies = (parent:string, m?: string[]) => {
        const modifiers = (m || [])
            .filter( x => x)
            .map( x => `${parent}--${x}` );
            
        return modifiers.join(' ');
    }

    const block = (m?: string[]) => {
        const modifiers = generateModifies(b, m); 
        return `${b} ${modifiers}`;
    }

    const element = (e: string, m?: string[]) => {
        const element = `${b}__${e}`;
        const modifiers = generateModifies(element, m); 
        return `${element} ${modifiers}`;
    }
    
    return {
        block,
        element
    }
}

export default bem;