enum LoggerType {
    WARNING = 'warn',
    ERROR = 'error',
    INFO = 'info',
    LOG = 'log'
}

const logger = () => {
    const logger = (type: LoggerType) => {
        if(process.env.NODE_ENV === 'development')return console[type];
        return (x:any) => {}
    }

    return {
        warning: (toLog: any) => logger(LoggerType.WARNING)(toLog),
        error: (toLog: any) => logger(LoggerType.ERROR)(toLog),
        info: (toLog: any) => logger(LoggerType.INFO)(toLog),
        log: (toLog: any) => logger(LoggerType.LOG)(toLog)
    }
}

export default logger();