<template>
    <div class="import">
        
        <div class="import__controls">
            <UITab 
                type="toggle"
                :items="tabItems" 
                :activeItem="activeTab" 
                @onSelect="tabOnSelect($event)"
            />

            <UIInputSearch class="import__search" placeholder="Enter keywords or item number" size="medium"/>
        </div>

        <br/>        
        <UIDivider/>
        
        <div class="import__table">
            <UITableRow class="import__table-head">
                <UITableColumn>
                    <label>Shape</label>
                </UITableColumn>

                <UITableColumn>
                    <label>System ID</label>                    
                </UITableColumn>

                <UITableColumn>
                    <label>Reference</label>
                </UITableColumn>

                <UITableColumn>
                    <label>Name</label>
                </UITableColumn>

                <UITableColumn>
                    <label>Submitted Date</label>                    
                </UITableColumn>

                <UITableColumn align="right" :last="true">
                    <label>Actions</label>
                </UITableColumn>
            </UITableRow>

            <UITableRow v-for="item in tableData" :key="item.id">
                <UITableColumn>
                    <img :src="getImage('shape')" alt="" class="import__item-image">
                </UITableColumn>

                <UITableColumn>
                    <label>{{item.systemId}}</label>
                </UITableColumn>

                <UITableColumn>
                    <label>{{item.reference}}</label>
                </UITableColumn>

                <UITableColumn>
                    <label>{{item.name}}</label>
                </UITableColumn>

                <UITableColumn>
                    <label>{{item.submittedDate}}</label>
                </UITableColumn>

                <UITableColumn align="right" :last="true" class="import__item-action">
                    <a>Open</a> 
                    <span>|</span>
                    <a>Delete</a>
                </UITableColumn>
            </UITableRow>
        </div>
        
        <UIPagination
            class="import__pagination"
            :totalItems="25" 
            :itemsPerPage="5" 
            :currentPage="paginationPage"
            @pageChanged="onPageChanged($event)"    
        />

    </div>
</template>

<script lang="ts" src="./import.ts"></script>
<style lang="scss" src="./import.scss" scoped></style>