<template>

  <div class="app">

    <div class="app__header">
      <h2>Shape design tool</h2>
      <UITab :items="tabItems" :activeItem="activeTab" @onSelect="tabOnSelect($event)"/>
    </div>

    <UIDivider/>

    <div class="app__tab-content" v-if="activeTab">
      <Standard v-if="activeTab.id === 'standard'"      class="app__tab-item" />
      <Import   v-else-if="activeTab.id === 'import'"   class="app__tab-item" />
      <Upload   v-else-if="activeTab.id === 'upload'"   class="app__tab-item" />
      <Library  v-else-if="activeTab.id === 'library'"  class="app__tab-item" />
      <Drawing  v-else-if="activeTab.id === 'drawing'"  class="app__tab-item" />
      <Tracer   v-else class="app__tab-item" />

    </div>


    <p class="app__instruction">Choose a shape from the origin top menu.</p>

  </div>



</template>

<style lang="scss" src="./styles/app.scss"></style>

<script lang="ts">
import { defineComponent } from 'vue';
import Tracer from '@/components/tracer/Tracer.vue';
import Standard from '@/components/standard/Standard.vue';
import Import from '@/components/import/Import.vue';
import Upload from '@/components/upload/Upload.vue';
import Library from '@/components/library/Library.vue';
import Drawing from '@/components/drawing/Drawing.vue';


import UITab from '@/components/ui/UITab.vue';
import iTabItem from '@/interface/tab-item.interface';
import UIDivider from '@/components/ui/UIDivider.vue';



export default defineComponent({
    name: 'App',
    components: {
      Tracer,
      Standard,
      Import,
      Upload,
      Drawing,
      Library,
      UITab,
      UIDivider
    },
    data() {
      const data: {
        activeTab: iTabItem | null,
        tabItems: iTabItem[]
      } = {
        activeTab: null,
        tabItems: [
          { id: 'standard', label: 'Standard' },
          { id: 'import', label: 'Import' },
          { id: 'upload', label: 'Upload' },
          { id: 'library', label: 'Library' },
          { id: 'drawing', label: 'Drawing' },
          { id: 'tracer', label: 'Tracer' },
        ]
      }

      return data;
    },
    methods: {
      tabOnSelect(item: iTabItem | null) {
        this.activeTab = item;
      }
    }
});
</script>