<template>
    <input :class="bem().block(modifiers())" v-bind:placeholder="placeholder"/>
</template>


<script lang="ts">
import bem from '@/utilities/bem';
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'UIInput',
    props: {
        placeholder: String,
        type: {
            type: String,
            default: 'text',
            validator: function (value: string) {
                return ['text', 'password', 'date', 'number'].indexOf(value) !== -1
            }
        },
        size: {
            type: String,
            default: 'large',
            validator: function (value: string) {
                return ['large', 'medium', 'small'].indexOf(value) !== -1
            }
        },
    },
    data(){
        return {
            bem: () => {
                return bem('ui-input')
            },
            modifiers: () => {
                return [
                    this.type,
                    this.size,
                    ...(this.active ? ['active'] : []),
                    ...(this.curve ? ['curve'] : [])
                ];
            }
        }
    }
});
</script>


<style lang="scss" scoped>
.ui-input{
    border: none;
    outline: none;
    font-size: 1.6rem;
    padding: 0 1.2rem;
    height: 5.5rem;
    color: #333;
    transition: 0.3s ease all;
    border-radius: .8rem;

    box-shadow: inset .5rem .5rem 1rem rgba(0,0,0,.10), inset -.5rem -.5rem 1rem #fff ;

    background-color: transparent;
    text-shadow: 1px 1px 0 #fff;
    color: rgba(0,0,0,0.87);

    min-width:var(--btn-width);
    height:var(--btn-height);
    font-size:var(--btn-font-size);

    


    // SIZE
    &--large {
        --btn-width: 25rem;
        --btn-height: 5.5rem;
        --btn-font-size: 1.6rem;
    }
    
    &--medium {
        --btn-width: 20rem;
        --btn-height: 4.5rem;
        --btn-font-size: 1.4rem;

    }

    &--small {
        --btn-width: 14rem;
        --btn-height: 3.5rem;
        --btn-font-size: 1.2rem;
    }
}
</style>


