
import bem from '@/utilities/bem';
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'UILoading',
    props: {
        text: String,
        show: Boolean
    },
    data(){
        return {
            bem: () => {
                return bem('ui-loading')
            },
            modifiers: () => {
                return [
                    ...(this.show ? ['show'] : [])
                ];
            }
        }
    }
});
