const asset = () => {
    const getImage = ({
        path,name,ext
    }: {
        path?: string,
        name: string,
        ext: 'png' | 'jpg' | 'jpeg'
    }) => {
        return require(`@/assets/images/${path || ''}${name}.${ext}`)
    }

    return {
        getImage
    }
}

export default asset();