<template>
    <div :class="bem().block([
        align,
        last ? 'last' : ''
    ])">
        <slot></slot>
    </div>
</template>


<script lang="ts">
import bem from '@/utilities/bem';
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'UITableColumn',
    props: {
        align: {
            type: String,
            default: 'left',
            validator: function (value: string) {
                return ['left', 'right', 'center'].indexOf(value) !== -1
            }
        },
        last: Boolean
    },
    data(){
        return {
            bem: () => {
                return bem('ui-table-column')
            }
        }
    }
});
</script>


<style lang="scss" scoped>
.ui-table-column {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
    min-height: 2.5rem;
    position: relative;
    margin-right: .5rem;

    &--right {
        justify-content: flex-end;
    }
    
    &--center {
        justify-content: center;
    }

    &::before{
        content: '';
        box-shadow: inset 2px 1.5px 1px rgba(88,108,138,.2), inset -4px -4px 10px #fff;
        background-color: #fff;
        width: 4px;
        height: 100%;
        position: absolute;
        right: 0;
    }

    &--last {
        margin-right: 0;
        &::before{ 
            display: none;
        }
    }
}
</style>




