<template>
    <button type="button" :class="bem().block(modifiers())">
        <span class="ui-button__text">{{text}}</span>
    </button>
</template>


<script lang="ts">
import bem from '@/utilities/bem';
import { defineComponent } from 'vue';


export default defineComponent({
    name: 'UIButton',
    props: {
        text: String,
        size: {
            type: String,
            default: 'large',
            validator: function (value: string) {
                return ['large', 'medium', 'small'].indexOf(value) !== -1
            }
        },
        type: {
            type: String,
            default: 'default',
            validator: function (value: string) {
                return ['default', 'primary', 'secondary'].indexOf(value) !== -1
            }
        },
        active: {
            type: Boolean,
            default: false
        },
        curve: {
            type:Boolean,
            default: false
        },
    },
    data(){
        return {
            bem: () => {
                return bem('ui-button')
            },
            modifiers: () => {
                return [
                    this.type,
                    this.size,
                    ...(this.active ? ['active'] : []),
                    ...(this.curve ? ['curve'] : [])
                ];
            },
        }
    }
});
</script>


<style lang="scss" scoped>
.ui-button{
    border: none;
    transition: 0.3s ease all;
    outline: none;
    cursor: pointer;
    padding: .7rem 1.4rem;
    border-radius: .8rem;
    font-weight: bold;
    border: 1px solid;
    position: relative;
    overflow: hidden;

    width:var(--btn-width);
    height:var(--btn-height);
    font-size:var(--btn-font-size);

    &__text{
        z-index: 2;
        position: relative;
    }

    &::before{
        content: '';
        width: 100%; 
        height: 100%;
        position: absolute;
        top: -1rem;
        left: -1rem;
        background: rgba(rgb(173, 173, 173), .4);
        transition: all .25s ease;
        opacity: 0;
        transform: translateX(-100%);
        border-top-right-radius: 3rem;
        border-bottom-right-radius: .5rem;
    }

    &:active{
        border-color: rgba(rgb(173, 173, 173), .3);
    }

    &:active::before{
        opacity: .9;
        transform: translateX(0%);
        width: 150%;
        height: 150%;
    }

    &--default{
        color: #fff;
        background-color: #555555;

        &:hover{
            background-color: #434343;
        }
    }

    // PRIMARY BUTTON
    &--primary {
        border-color: #0088ce;
        background: linear-gradient(-10deg, #0d52a0 0%, #0088ce 100%);
        color: #fff;
        // box-shadow: 3px 3px 10px rgba(#24415D,.3), -4px -4px 10px rgba(#fff,.8); 
        box-shadow:  4px 4px 7px rgba(0,0,0,.2), -2px -2px 10px #fff;

        &::before{
            background-color: rbga(#fff,.6);
        }

        &.ui-button--active{
            background:#fff;
            box-shadow: inset 3px 3px 10px rgba(#586C8A ,.5), inset -4px -4px 10px rgba(#fff,1); 
            color: #0088CE;      
            border:none; 
        }
    }

    
    // SECONDARY BUTTON
    &--secondary {
        border-color: #f9f9f9;
        background: linear-gradient(142deg, rgba(215,213,213,0.2) 0%, rgba(255,255,255,0.1) 50%);
        box-shadow: 9px 9px 21px rgba(#000, .1), -8px -8px 7px #fff;

        color: #5e6a71;

        // &:active,        
        &.ui-button--active{
            background:#0088ce;
            box-shadow: inset 5px 5px 10px rgba(#000, .2), inset 0px 0px 2px #fff;
            color: #fff;      
            border:none; 
        }
    }



    &--curve{
        border-radius: 5rem;
    }



    // SIZE
    &--large {
        --btn-width: 25rem;
        --btn-height: 5.5rem;
        --btn-font-size: 1.6rem;
    }
    &--medium {
        --btn-width: 20rem;
        --btn-height: 4.5rem;
        --btn-font-size: 1.4rem;

    }
    &--small {
        --btn-width: 14rem;
        --btn-height: 3.5rem;
        --btn-font-size: 1.2rem;
    }
}
</style>


