
import bem from '@/utilities/bem';
import { defineComponent } from 'vue';


export default defineComponent({
    name: 'UIButton',
    props: {
        text: String,
        size: {
            type: String,
            default: 'large',
            validator: function (value: string) {
                return ['large', 'medium', 'small'].indexOf(value) !== -1
            }
        },
        type: {
            type: String,
            default: 'default',
            validator: function (value: string) {
                return ['default', 'primary', 'secondary'].indexOf(value) !== -1
            }
        },
        active: {
            type: Boolean,
            default: false
        },
        curve: {
            type:Boolean,
            default: false
        },
    },
    data(){
        return {
            bem: () => {
                return bem('ui-button')
            },
            modifiers: () => {
                return [
                    this.type,
                    this.size,
                    ...(this.active ? ['active'] : []),
                    ...(this.curve ? ['curve'] : [])
                ];
            },
        }
    }
});
